
import { defineComponent } from "vue";

import NavProfile from "./NavProfile.vue";

export default defineComponent({
  name: "CNavbar",
  components: {
    NavProfile,
  },
  data() {
    return {
      navIsOpen: false as boolean,
    };
  },
  methods: {
    triggerNav(): void {
      this.navIsOpen = !this.navIsOpen;
    },

    getNavIsOpen(): boolean {
      return this.navIsOpen;
    },
  },
});
